html {
  box-sizing: border-box;
  /* 767px */
  overflow: -moz-scrollbars-vertical;
  overflow-y: scroll;
  min-height: 100%;
  height: 100%;
  font-size: 62.5%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }
  @media only screen and (max-width: 47.938em) {
    html {
      font-size: 55%; } }

body {
  margin: 0;
  padding: 0;
  min-height: 100%;
  height: 100%;
  background: #fff;
  color: #444;
  letter-spacing: 0.03em;
  font-size: 1.4em;
  font-family: "Muli", sans-serif;
  line-height: 1.5; }

* {
  box-sizing: inherit; }
  *:before, *:after {
    box-sizing: inherit; }

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none; }

::selection {
  background: #b3d4fc;
  text-shadow: none; }

::-webkit-selection {
  background: #b3d4fc;
  text-shadow: none; }

::-ms-selection {
  background: #b3d4fc;
  text-shadow: none; }

::-m4r10-l0nc4rek {
  color: #BADA55;
  display: flex; }

input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none; }

a {
  transition: all 0.2s ease 0s;
  color: #0071bc;
  text-decoration: none; }
  a:hover {
    color: tint(#0071bc, 40%); }
  a.alt {
    color: #333; }

